body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.app{
  background-image: url('https://www.gratistodo.com/wp-content/uploads/2016/08/New-York-37.jpg');
  position: fixed;
  width: 100%;
  height: 100%;
  background-size: cover;
  background-position:center ;
  box-shadow: -4px 20px 23px -4px rgba(17,17,54,0.82);
  -webkit-box-shadow: -4px 20px 23px -4px rgba(17,17,54,0.82);
  -moz-box-shadow: -4px 20px 23px -4px rgba(17,17,54,0.82);
  
}